@import url('https://fonts.googleapis.com/css2?family=Anton&family=Architects+Daughter&family=Pacifico&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anton&family=Architects+Daughter&display=swap');
@import './mediaquery/MediaQuery';
.nav-container {
  font-weight: 900;
  font-size: 1.1rem;
  position: sticky;
  height: 60px;
  z-index: 999;
  display: flex;
  .logo {
    font-family: 'Anton', sans-serif;
    font-family: 'Architects Daughter', cursive;
    font-family: 'Pacifico', cursive;
    h1 {
      cursor: default;
      padding: 0.5rem 2rem;
      font-weight: 100;
      color: rgb(48, 50, 75);
    }
  }
  i {
    position: absolute;
    top: 20%;
    right: 3%;
    font-size: 1.5rem;
    cursor: pointer;
    display: none;
    @include tablet {
      display: block;
    }
  }
  .nav-list {
    position: absolute;
    right: 0;
    font-family: 'Anton', sans-serif;
    font-family: 'Architects Daughter', cursive;
    width: calc(100vw - 40%);
    height: 60px;
    display: flex;
    justify-content: space-around;
    .contact-link {
      margin-right: 7%;
    }
    .balloon {
      position: absolute;
      width: 40px;
      height: 40px;
      top: 15%;
      right: 1%;
      opacity: 1;
    }
    @include tablet {
      display: none;
    }
  }

  .sidebar {
    font-family: 'Anton', sans-serif;
    font-family: 'Architects Daughter', cursive;
    background-color: rgb(131, 130, 93);
    border-radius: 3%;
    position: absolute;
    right: 0;
    margin-top: 60px;
    height: 0;
    display: none;
    width: 30vw;
    overflow: hidden;
    display: flex;
    flex-flow: column;
    justify-content: center;
    transition: height 0.5s;

    li {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0;
      width: 100%;
      &:hover {
        text-decoration: underline;
        background-color: rgb(238, 231, 231);
      }
      &.contact {
        margin: 0;
      }
    }
    img {
      display: none;
    }
  }

  li {
    margin: 0 auto;
    padding: 1rem 1rem;
    height: 60px;
    cursor: pointer;
    align-self: center;
    &:hover {
      text-decoration: underline;
    }
    &.home {
      position: relative;
      .red-circle {
        position: absolute;
        top: 4px;
        left: -24px;
        width: 120px;
        height: 50px;
        z-index: -1;
      }
    }
    &.about {
      position: relative;
      .red-circle {
        position: absolute;
        top: 4px;
        left: -24px;
        width: 120px;
        height: 50px;
        z-index: -1;
      }
    }
    &.skills {
      position: relative;
      .red-circle {
        position: absolute;
        top: 4px;
        left: -24px;
        width: 120px;
        height: 50px;
        z-index: -1;
      }
    }
    &.project {
      position: relative;
      .red-circle {
        position: absolute;
        top: 4px;
        left: -18px;
        width: 120px;
        height: 50px;
        z-index: -1;
      }
    }
    &.contact {
      position: relative;
      .red-circle {
        position: absolute;
        top: 4px;
        left: -20px;
        width: 120px;
        height: 50px;
        z-index: -1;
      }
    }
  }
}
